<template>
     <div class="container">
        <div class="main__info" v-if="todos">
            <div class="main__info-item" v-for="todo in todos" :key="todo.id">
                <h2>{{ todo.id }}</h2>
                <p>{{ todo.title }}</p>
                <p>{{ todo.completed }}</p>  
            </div>
        </div>
        <div v-else>
            Loading....
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                
            }
        },
        created() {
            this.getTodos()
        },
        computed: {
            todos() {
                return this.$store.state.todos
            }
        },
        methods: {
            getTodos() {
                this.$store.dispatch('getTodos')
            }
        },
    }
</script>

